.wrapper {
	margin: 0;
	padding: 0;
	font-family: Arial, Helvetica, sans-serif;
	box-sizing: border-box; }
nav {
	background: #000;
	display: flex;
	color: #ccac5b;
	height: 100px;
	align-items: center;

	.return_arrow {
		text-transform: uppercase;
		letter-spacing: 8px;
		font-size: 0.8rem;
		flex-grow: 1;
		padding-left: 2rem; }
	.logo_image {
		flex-grow: 2;
		padding-left: 2rem;
		img {
			width: 300px; } } }

.mission_info {
	padding: 4rem 2rem;
	display: flex;
	.half_grid {
		flex-grow: 1; }
	.date_launch {
		text-transform: uppercase;
		letter-spacing: 8px;
		color: #a7a9ac;
		margin-bottom: 2rem; }
	.rocket_name {
		text-transform: uppercase;
		font-size: 3.5rem; }
	.counter-rocket {
		text-transform: uppercase;
		margin-bottom: 2rem; }

	h4 {
		color: #ccac5b;
		text-transform: uppercase;
		letter-spacing: 8px;
		font-weight: 100; }
	hr {
		border: 0;
		border-top: 2px solid #ccac5b; } }

.mission_menu {
	box-sizing: border-box;
	background-image: url('../assets/rocket.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	width: 100%;
	overflow: hidden;
	min-height: 700px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-end;
	flex-wrap: wrap;
	color: #fff;
	.mission_content {
		width: 100%; }
	h3 {
		text-align: center;
		color: #fff;
		width: 100%;
		font-size: 2rem;
		text-transform: uppercase;
		letter-spacing: 25px;
		font-weight: 200; }
	.mission_links {
		background-color: transparent;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		flex-direction: row;
		a {
			color: #fff;
			text-transform: uppercase;
			letter-spacing: 5px;
			margin: 0 1rem;
			padding: 1rem 2rem;
			display: block;
			text-decoration: none;
			font-size: 1rem;
			border: 1px solid #ffffff;
			border-radius: 2em; } } }

footer {
	font-size: .8rem;
	letter-spacing: 2px;
	text-transform: uppercase;
	padding: 3rem 4rem;
	background: #22272b;
	color: #666666;
	display: flex;
	div {
		flex-grow: 1; }
	.links-media span:after {
		content: "|";
		padding-left: .5em; }
	.copyright {
		text-align: right; }
	a {
		color: #a5a7ab;
		text-decoration: none;
		margin-right: 0.5em; } }
